body {
  background-color: #f4f4f4;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  height: 100vh;
  margin: 0;
}

.container {
  background-color: #f0ffff;
  border-radius: 8px;
  padding: 20px;
  width: 400px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
  margin-top: 45px;
  min-height: 550px;
  flex-direction: column;
  position: relative;  
}

.task-item {
  margin-bottom: 10px;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
  display: flex;
  align-items: center;
}

.task-item .button-group {
  display: flex;
  align-items: center;
  margin-left: auto;
  margin-right: 10px;

}

.task-item.completed {
  background-color: #fae0d8;
}

.task-item button {
  background-color: #28a745;
  color: #fff;
  border: none;
  padding: 5px 10px;
  border-radius: 4px;
  cursor: pointer;
  margin-left: 5px;
}

.task-item::before {
  content: 'ðŸ”¹';
  margin-right: 10px;
}

.task-item.completed::before {
  margin-right: 10px;
}

.task-list {
  margin-bottom: 45px;
}

.new-task-input {
  position: absolute;
  bottom: 10px;
  display: flex;
  align-items: center;
}

.new-task-input input {
  flex: 1;
  padding: 14px;
  margin-right: 50px;
  border: 1px solid #ddd;
  border-radius: 4px;
  outline: none;
}

.new-task-input button {
  background-color: #007bff;
  color: #fff;
  border: none;
  padding: 14px 30px;
  border-radius: 4px;
  cursor: pointer;  
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding-top: 35px;
}

.popup-modal {
  background: #fff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  text-align: center;
}

.popup-modal p {
  margin-bottom: 15px;
}

.popup-modal button {
  background-color: #007bff;
  color: #fff;
  border: none;
  padding: 10px 15px;
  border-radius: 4px;
  cursor: pointer;
  margin-right: 5px;
}

.popup-modal button:last-child {
  margin-right: 0;
}